
.book-modal > .modal {
  position: fixed;
  top: 50%;
  left: 50%;
  max-height: 95%;
  padding: 1em;
  border-radius: 6px;
  background: #fff;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.16);
  overflow: auto;
  z-index: 2;
}
.book-modal > .modal > .top-info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 0.5em;
}
.book-modal > .modal > .top-info > .thumb {
      margin-right: 1em;
}
.book-modal > .modal > .top-info > .main-info {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.book-modal > .modal > .top-info > .main-info > span {
        margin-bottom: 0.5em;
}
.book-modal > .modal > .top-info > .main-info > span > strong {
          font-weight: bold;
}
.book-modal > .modal > .top-info > .main-info > .title {
        margin-bottom: 0.25em;
        font-size: 2em;
        font-weight: bold;
}
.book-modal > .modal > .top-info > .main-info > .authors {
        font-style: italic;
}
.book-modal > .modal > .description-label {
    margin-bottom: 0.5em;
    font-weight: bold;
}
.book-modal > .modal > .description {
    font-size: 0.9em;
}
.book-modal > .mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}



