
html,
body {
  height: 100%;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.main-content {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  overflow: hidden;
}
.search-area {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-bottom: 1em;
  overflow: auto;
}
.search-area > .title {
    margin: 0.5em 0 0.2em;
    text-align: center;
    font-weight: bold;
    font-size: 2.5em;
}
.search-area .search {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.search-area .search .search-bar {
      margin-right: 0.25em;
      font-size: 1.75em;
      padding: 0.25em 0.5em;
      border: 1px solid #ccc;
      border-radius: 6px;
      color: #2c3e50;
}
.search-area .search .button {
      padding: 0.5em;
      border: 0;
      border-radius: 6px;
      background: #2c3e50;
      font-size: 1.75em;
      color: #fff;
      transition: background 200ms linear;
}
.search-area .search .button:hover {
        background: #476481;
        cursor: pointer;
}
.search-area .highlight {
    border-radius: 2px;
    background: #ccc;
}
.pagination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.pagination > .page {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 0.25em;
}
.pagination > .page.active > button {
      border-color: transparent;
      color: #fff;
      background: #2c3e50;
}
.pagination > .page.active > button:hover {
        cursor: auto;
        color: #fff;
        background: #2c3e50;
}
.pagination > .page > button {
      display: block;
      width: 2em;
      padding: 0.5em 0;
      border: 1px solid #2c3e50;
      border-radius: 6px;
      background: transparent;
      color: #2c3e50;
      transition: background 200ms linear;
}
.pagination > .page > button:hover {
        background: rgba(0, 0, 0, 0.15);
        cursor: pointer;
}



