
.book {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 1em;
  padding: 1em 2.5em 1em 1em;
  border: 1px solid #ccc;
  border-radius: 6px;
  height: 8em;
}
.book:hover > .more-info {
    opacity: 1;
}
.book > .thumb {
    height: 100%;
    margin-right: 1em;
}
.book > .thumb > img {
      height: 100%;
}
.book > .more-info {
    position: absolute;
    bottom: 0.5em;
    left: 50%;
    font-size: 0.8em;
    font-weight: bold;
    opacity: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    transition: opacity 200ms linear;
}
.book > .more-info:hover {
      opacity: 0.7;
      cursor: pointer;
}
.book > .data {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.book > .data > .title {
      font-weight: bold;
      font-size: 1.4em;
}
.book > .data > .authors {
      margin-bottom: 1em;
      font-style: italic;
      font-size: 0.8em;
}
.book > .data > .description {
      position: relative;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      font-size: 0.8em;
      overflow: hidden;
}
.book > .data > .description::after {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to top, #fff, transparent);
        content: '';
}
.book > .data > .fav {
      position: absolute;
      top: 0;
      right: 0;
      color: #ccc;
      transition: color 100ms linear;
      border: 0;
      background: none;
      font-size: 2.5em;
      outline: none;
      transition: -webkit-transform 200ms linear;
      transition: transform 200ms linear;
      transition: transform 200ms linear, -webkit-transform 200ms linear;
}
.book > .data > .fav:active {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
}
.book > .data > .fav.active {
        color: #e4e40e;
}
.book > .data > .fav:hover {
        cursor: pointer;
}



